import tTable from '@/components/t-table/t-table.vue'
import tTh from '@/components/t-table/t-th.vue'
import tTr from '@/components/t-table/t-tr.vue'
import tTd from '@/components/t-table/t-td.vue'
export default {
	components: {
		tTable,
		tTh,
		tTr,
		tTd
	},
	data() {
		return {
			msg:'',
			uid: "",
			curr_worktype_name: '',
			curr_worktype_bh: '',
			chuanghao: '',
			huohao: '',
			worktype: "",
			sl: '',
			price: '',
			je: '',
			info: '',
			list: [],
			userid: '',
			color: '',
			chima: '',
			ganghao: '',
			dk: ''
		}
	},
	onBackPress(options) {
		if (options.from === 'navigateBack') {
			uni.switchTab({
				url: '/pages/index/index'
			});
			return true;
		}
		return false;
	},
	onLoad() {
		this.dk = this.websiteUrl,
			uni.getStorage({
				key: 'fwqsj',
				success: res => {
					this.dk = res.data
				}
			})
		uni.getStorage({
			key: 'loginData',
			success: res => {
				this.uid = res.data.uid,
					this.curr_worktype_bh = res.data.curr_worktype_bh
			}
		})


	},
	methods: {
		focus(){
			this.userid = '';
		},
		onpeSweepCode(){
			uni.scanCode({
				onlyFromCamera: true,
				scanType:['qrCode','barCode'],   //这里有个坑，不写的话扫描速度有点慢
				success: res => {
					this.userid = res.result,
					uni.request({
						url: `https://${this.dk}/getWorktypeProgress.aspx`,
						method: 'GET',
						data: {
							userid:this.userid
						},
						complete: res => {
							if(res.statusCode == 200){
								this.list = res.data;
								this.huohao = '';
								this.color = '';
								this.chima = '';
								if(this.list.length>0){
									let temp =  res.data;
									this.huohao = temp[0].huohao
									this.color = temp[0].color
									this.chima = temp[0].chima
									this.chuanghao = temp[0].chuanghao
									uni.showToast({
										 position:'top',
										 title: '扫描成功',
										 icon :'none',
									     duration: 5000
										 
									})
								}
							}else{
								this.list = [];
								this.huohao = '';
								this.color = '';
								this.chima = '';
								this.chuanghao = '';
								uni.showToast({
									 position:'top',
									 title:'请扫描正确的条码',
									 icon :'none',
									 duration: 3000
								})
							}
						}
					});
				}
			})
		},
		getData() {
			uni.request({
				url: `https://${this.dk}/getWorktypeProgress.aspx`,
				method: 'GET',
				data: {
					userid:this.userid
				},
				complete: res => {
					//console.log(res);
					if (res.statusCode == 200) {
						this.list = res.data;
						this.huohao = '';
						this.color = '';
						this.chima = '';
						if(this.list.length>0){
							let temp =  res.data;
							this.huohao = temp[0].huohao
							this.color = temp[0].color
							this.chima = temp[0].chima
							this.chuanghao = temp[0].chuanghao
							uni.showToast({
								 position:'top',
								 title: '扫描成功',
								 icon :'none',
							     duration: 5000
								 
							})
							
						}
					} else {
						this.list = [];
						this.huohao = '';
						this.color = '';
						this.chima = '';
						this.chuanghao = '';
						uni.showToast({
							 position:'top',
							 title:'请求数据失败，请输入正确的条码',
							 icon :'none',
						     duration: 3000
						})
					}
				}
			})
		}
	}
}
